ilidad, y es frecuente que los usuarios puedan vagar de una página a otra con el peligro de perderse en la profundidad de una jerarquía web. La manera de buscar y la frecuencia con la que el usuario puede perderse en este proceso son aspectos que deben ser tenidos en cuenta a la hora de diseñar los sistemas de navegación web (Calvo, 2015).")])]),_c('div',{staticClass:"col-lg-5"},[_c('figure',[_c('img',{staticClass:"mx-auto",staticStyle:{"max-width":"491px"},attrs:{"src":require("@/assets/curso/temas/58.png")}})])])])])]),_c('div',{staticClass:"row justify-content-center"},[_c('div',{staticClass:"col-lg-11"},[_c('div',{staticClass:"row align-items-center"},[_c('div',{staticClass:"col-lg-7 mb-4 mb-lg-0"},[_c('h4',{staticClass:"mb-5"},[_c('span',{staticClass:"color-sistema-l"},[_vm._v("B.")]),_vm._v(" Navegar por sitios web")]),_c('p',{staticClass:"mb-0"},[_vm._v("La información no solo se busca, se experimenta. El término «experiencia» implica que las emociones juegan un papel clave junto con la conducta y la cognición. Las personas ya no pueden ser vistas como seres racionales que se comportan de forma sistemática y ordenada a la hora de buscar información, teniendo en cuenta su papel crítico en la web. Al respecto muchos de los sitios tienen en la barra de navegación etiquetas o secciones con los siguientes títulos (Inicio, servicios, nosotros, acerca de y contactos), esta distribución del contenido que hace parte de la arquitectura de la información de un sistema interactivo "),_c('em',[_vm._v("software")]),_vm._v(" debe evaluarse, inicialmente.")])]),_c('div',{staticClass:"col-lg-5"},[_c('figure',[_c('img',{staticClass:"mx-auto",staticStyle:{"max-width":"491px"},attrs:{"src":require("@/assets/curso/temas/13.png")}})])])])])]),_c('div',{staticClass:"row justify-content-center"},[_c('div',{staticClass:"col-lg-11"},[_c('div',{staticClass:"row align-items-center"},[_c('div',{staticClass:"col-lg-7 mb-4 mb-lg-0"},[_c('h4',{staticClass:"mb-5"},[_c('span',{staticClass:"co